































































export default {
	data() {
		return {
			mun:''
		}
	},
	onLoad() {
		var phone = uni.getStorageSync('phone');
		this.mun = phone.replace(/^(\d{3})\d{4}(\d+)/,"$1****$2");
	},
	methods:{
		jump_jilu(){
			uni.navigateTo({
				url:'../my/youlan'
			})
		},
		jump_xieyi(){
			uni.navigateTo({
				url:'../my/xieyi'
			})
		},
		jump_fankui(){
			uni.navigateTo({
				url:'../my/fankui'
			})
		},
		jump_about(){
			uni.navigateTo({
				url:'../my/about'
			})
		},
		jump_login(){
			var token = uni.getStorageSync('token');
			uni.request({
				url:'http://api.xinmicredit.cn/user/logout',
				method:'POST',
				header:{'Authorization': token},
				success: (res) => {
					console.info(res)
					uni.clearStorageSync();
				}
			})
			uni.navigateTo({
				url:'../banner/banner'
			})
		}
	}
}
